
@import './../../styles/vars.scss';

.comment{
  &__header{
    display: flex;
  }

  &__img{
    border: 2px solid #FFFFFF;
    box-sizing: border-box;
    filter: drop-shadow(0px 10px 20px $darkShade-1);
    border-radius: 32px;
    max-width: 48px;
    height: 48px;

    img{
      width: 100%;
      height: 100%;
      border-radius: 32px;
      object-fit: cover;
    }
  }
  &__info {
    padding-left: 10px;
  }

  &__person-name{
    font-weight: bold;
    color: $darkShade-75;
  }

  &__content{
    display: flex;
    margin-top: 10px;
  }

  &__like{
    margin-top: 2px;
    display: flex;
    justify-content: center;
    min-width: 48px;
  }

  &__text{
    padding-left: 10px;
  }
}